@media (max-width: 1400px) {
  .floating-items {
    display: none;
  }
}
@media (max-width: 1200px) {
  .property-card {
    &__body {
      &-parameter {
        width: 100%;
      }
    }
  }
}
@media (max-width: 992px) {
  .navbar-collapse {
    background: rgba(66, 62, 55, 0.9);
    padding: 15px 20px;
    border-radius: 20px;
  }
  .service-card {
    &__content {
      width: 100%;
    }
  }
}
@media (max-width: 768px) {
  .property-card {
    &__body {
      &-parameter {
        width: 50%;
      }
    }
  }
  .service-card {
    &__content {
      width: 50%;
    }
  }

  .floating-splash-logo {
    display: none;
  }

  .contacts-section {
    background-position: bottom center;
  }
  .map-container {
    width: 100%;
    height: 500px;
  }
}
@media (max-width: 576px) {
  .property-card {
    &__body {
      &-parameter {
        font-size: 0.85rem;
      }
    }
  }

  .hero-title {
    font-size: 35px;
  }

  .section-title {
    font-size: 35px;
  }

  .service-card {
    &__content {
      width: 75%;
    }
    &__title {
      font-size: 1.25rem;
    }
  }

  .main-footer__copyright {
    text-align: center;
  }
}
